/*
* Style Works for Editor Only
*/
.aa-custom-spacer {
  width: 100%;
  height: 1px;
  background: #e3e3e3;
  margin: 16px 0;
}

.wp-block-aab-group-accordion {
  margin-bottom: 16px;
}

.wp-block-aab-accordion-item {
  .aagb__accordion_body {
    display: block !important;

    &.aagb__accordion_body--show {
      display: block !important;
    }
  }
}


.aagb_form_control {
  color: #848d95 !important;
}

.wp-block-aab-accordion-item .aagb__accordion_body .aagb__accordion_component .block-editor-block-list__layout {
  p:has(+ div) {
    margin-bottom: 4px;
  }

  p {
    margin-top: 0 !important;
  }
}

.wp-block-aab-group-accordion .show-more-btn-container{
  margin-top: -10px !important;
}
.block-editor-block-list__block.aagb__accordion_container.check-list.wp-block-aab-accordion-item {
  .aagb__accordion_head {
    .aagb__accordion_heading {
      .checklist-label {
        position: relative;
        width: 25px;
        height: 25px;
        border: 1px solid #e6e6e6;
        background-color: #fff;
        cursor: pointer;
        margin-right: 10px;
        border-radius: 4px;

        span {
          width: 25px;
          height: 25px;
          position: absolute;
          top: 0;
          left: 0;
        }

        span:after {
          content: "âœ“";
          display: block;
          text-align: center;
          font-size: 16px;
          color: #0707a3;
        }
      }
    }
  }
}

.wp-block-aab-group-accordion .block-editor-block-list__block.aagb__accordion_container.style-qa .aagb__accordion_head .aagb__accordion_heading .icon-container .icon-a{
  top: 43px;
  opacity: 1;
}